import React, { Component } from 'react';
import OnlineConsultation from './OnlineConsultation';
import Workshops from './Workshops';
import Colaborations from './Colaborations';
import './Info.css';

class Info extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 0
    };
  }

  componentDidMount() {
    var tabs = document.querySelectorAll('.service-tab');
    tabs.forEach(function(tab, index) {
      tab.addEventListener('click', function() {
        tabs.forEach(function(tab) {
          tab.classList.remove('selected');
        });
        tab.classList.add('selected');
        this.setState({
          selectedTab: index
        });
      }.bind(this));
    }.bind(this));
  }

  getContent(selectedTab) {
    if (selectedTab === 0) {
      return <OnlineConsultation />;
    }

    if (selectedTab === 1) {
      return <Workshops />;
    }

    if (selectedTab === 2) {
      return <Colaborations />;
    }
  }

  render() {
    const content = this.getContent(this.state.selectedTab);

    return (
      <div className='Info'>
        <div className='services-row'>
          <div id='personalized-consultation' className='service-tab selected'>
            <div className='service-image'></div>
            <div className='service-title'>
              Consulta personalizada, online y presencial
            </div>
          </div>
          <div id='workshops' className='service-tab'>
            <div className='service-image'></div>
            <div className='service-title'>
              Formaciones y talleres
            </div>
          </div>
          <div id='colaborations' className='service-tab'>
            <div className='service-image'></div>
            <div className='service-title'>
              Restauración colectiva y empresas
            </div>
          </div>
        </div>
        { content }
      </div>
    );
  }
}

export default Info;
